@font-face {
    font-family: 'sulu';
    src:
        url('fonts/sulu.ttf?74wnpt') format('truetype'),
        url('fonts/sulu.woff?74wnpt') format('woff'),
        url('fonts/sulu.svg?74wnpt#sulu') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
}

[class^="su-"], [class*=" su-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'sulu' !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

